/* Search bar */
.search-div{
    width: auto;
    height: 80vh;
    align-items: center;
    display: flex;
    justify-content: center;
}
.search-div input{
    background: transparent;
    border: solid;
    border-color: black;
    border-width: 1.5px ;
    border-radius: 50px;
    height: 5vh;
    outline: none;
    margin-top: 10vh;
}
.search-div input:focus{
  background-color: white;
}
.search-bar {
    position: relative;
    display: flex;
    align-items: center;
    width: 100vh;
    height: auto;
    margin: 20px auto;
  }

  .search-bar input[type="text"] {
    flex-grow: 1;
    height: 40px;
    float: left;
    border-radius: 20px;
    padding: 0 15px;
    font-size: 16px;
  }

  .search-bar button[type="submit"] {
    height: 40px;
    width: 40px;
    border: none;
    background-color: transparent;
    cursor: pointer;
    padding: 0;
    margin-top: 10vh;
  }

  .search-bar button[type="submit"]:focus,
  .search-bar button[type="submit"]:active {
    outline: none;
    box-shadow: none;
  }
  .search-bar button i{
    color: rgb(0, 0, 0);
  }

.App {
    width: auto;
    height: auto;
  }

  .art{
    height: 40vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .labelOfSearch{
    position: absolute;
    width: 5vh;
    height: 20vh;
    background-color: transparent;
    border-radius: 50px;
    box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.5);
  }
  .sh4{
    position: absolute;
    font-family: 'Poppins', sans-serif;
    text-shadow: 2px 3px 5px rgba(0, 0, 0, 0.5);
    text-align: center;
    line-height: 2.5vh;
  }
  .sh4 span{
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    font-size: 5vh;
    color:rgb(255, 255, 255);
    text-shadow: 2px 3px 5px rgba(0, 0, 0, 0.5);
  }
  .imgOfSearch{
    padding-top: 15vh;
    padding-left: 40vh;
  }
  .imgOfSearch img{
    width: 10vh;
  }
  
  .search-bar-container {
    padding:30px;
    /* padding-top: 30vh; */
    width: 70%;
    margin: auto;
    flex-direction: column;
    align-items: center;
    
  }
  .home-left{
    
    width: 50%;
    padding:15vh 20px;
    padding-bottom: 0;
    float: left;
  }
  .home-right{
    width: 50%;
    float: right;
    padding: 20px;
    padding-top: 0;
  }
  .discripton{
    position: absolute;
    top: 35vh;
    display: flex;
    justify-content: center;
    height: auto;
  }

  .dis-card{
    width: 70%;
    height: auto;
    background-color: #36363667;
    padding:7vh;
    border-radius: 10px;
  }
  .dis-card p{
    color: white;
  }
  .imgContainer{
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 5rem;
  }

  .imgContainer img{
    width: 75vh;
  }
  .HeadLine{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: start;
    padding-top: 15vh;
  }
  .Headline-text h2{
    position: relative;
    color: #131313;
    font-size: 3rem;
    width: 100%;
    line-height: 65px;
  }
  .HeadLine h1{
    position: relative;
    margin: auto;
    font-size: 8.5vh;
    line-height: 8vh;
    letter-spacing:0px;
  }
  .HeadLine{
    display: flex;
    flex-direction: column;
  }
  .disco{
    width: 100%;
  }
  .disco .Description{
    padding: 30px;
    /* padding-top: 30vh; */
    width: 70%;
    margin: auto;
  }
  .Headline-text{
    padding: 30px;
    /* padding-top: 30vh; */
    width: 70%;
    margin: auto;
  }
  .Description{
    padding-top: 5vh;
    width: 80%;
  }
  .Description p{
    font-size: 2.5vh;
    font-family: 'Poppins', sans-serif;
  }
  .Description p span{
    color: brown;
  }
  
  
  .sub-span-a{
    color: green;
    font-weight: normal;
  }
  .sub-span-u{
    color: cornflowerblue;
    font-weight: normal;
  }
  .search-text{
    font-weight: normal;
  }
.my-button {
  border: none;
  padding: 0;
  background: none;
  font-family: inherit;
  cursor: pointer;
  outline: none;
}

/* Custom button styles */
.my-button {
  padding: 10px 20px;
  background-color: #4CAF50;
  color: #fff;
  border-radius: 4px;
  font-size: 16px;
}

.my-button:hover {
  background-color: #45a049;
}

.my-button:focus {
  outline: none;
  box-shadow: 0 0 0 2px #4CAF50; /* Add a focus outline */
}
.button0 {
  border: none;
  color: white;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
}

.button1 {
  background-color: teal;
  color: white;
  border: 2px solid teal;
  border-radius: 0px;
}

.button1:hover {
  background-color:  rgb(8, 85, 85);
  color: white;
}
.add-button{
  padding-top: 1rem;
}

.input-wrapper {
  width: 100%;
  height: 2.5rem;
  border: solid;
  border-width: 1px;
  border-radius: 10px;
  border-color: rgb(66, 66, 66);
  padding: 0 15px;
  box-shadow: 0px 0px 8px #ddd;
  background-color: transparent;
  display: flex;
  align-items: center;
}

.search-input {
  background-color: transparent;
  border: none;
  height: 100%;
  font-size: 1.25rem;
  width: 100%;
  margin-left: 5px;
}

.search-input:focus {
  outline: none;
}

#search-icon {
  color: brown;
}

@media (max-width: 780px){
  .home-right{
    float: none;
    width: 100%;
  }
  .home-left{
    float: none;
    width: 100%;
  }
  .imgContainer img{
    width: 100%;
  }
  .HeadLine{
    padding-top: 5vh;
    width: auto;
  }
  .Headline-text{
    padding:0 ;
    width: 90%; 
  }
  .HeadLine h1{
    font-size: 8.5vh;
    line-height: 8vh;
    letter-spacing:0px;
  }
  .discripton{
    width: 100%;
    top: 10%;
    justify-content: center; 
    position: relative;
  }
  .imgContainer{
    display: none;
  }
  .dis-card{
    width: 80%;
    padding: 2vh;
  }
  .search-bar-container{
    padding: 30px;
    width: 100%;
  }
}
@media (max-width: 500px){
  .home-right{
    float: none;
    width: 100%;

  }
  .home-left{
    float: none;
    width: 100%;
  }
  .HeadLine{
    padding-top: 5vh;
  }
  .Headline-text{
    padding:0 ;
    width: 85%; 
  }
  .Headline-text h2{
    font-size: 2.5rem;
    width: 100%;
    line-height: 50px;
  }
}